<template>
  <div class="sign-in-up">
    <div class="content">
      <div class="mb-4 sign-in-up-header">
        <h1>Register</h1>
        <button class="btn-link" @click="directToSignIn">
          Already a member
        </button>
      </div>
      <input
        v-model="user.name"
        type="text"
        class="custom-input mb-4"
        placeholder="Please type your name"
      />
      <input
        v-model="user.passcode"
        type="password"
        maxlength="4"
        class="custom-input mb-5"
        placeholder="Please type your 4 digit passcode"
      />
      <p class="error" v-if="errorMessage !== ''">{{ errorMessage }}</p>
      <button @click="signUp" class="proceed-btn">
        Register
        <b-spinner v-if="loading" class="action-spinner"></b-spinner>
      </button>
    </div>
  </div>
</template>

<script>
import UserService from '../service/UserService';

export default {
  name: 'SignUp',
  data() {
    return {
      user: {
        name: '',
        passcode: '',
      },
      errorMessage: '',
      loading: false,
    };
  },
  created() {},
  methods: {
    signUp() {
      this.loading = true;

      UserService.signup(this.user).then(
        () => {
          this.$router.push({
            name: 'SignIn',
            params: { userFromSignUp: this.user },
          });
          this.loading = false;
        },
        (error) => {
          this.errorMessage = error.response.data;
          this.loading = false;
        }
      );
    },
    directToSignIn() {
      this.$router.push({ name: 'SignIn' });
    },
  },
};
</script>

<style lang="scss" scoped>
@import '../assets/styles/main.scss';
</style>
