<template>
  <div class="sign-in-up">
    <div class="content">
      <div class="mb-4 sign-in-up-header">
        <h1>Sign In</h1>
        <button class="btn-link" @click="directToSignUp">
          Create Account
        </button>
      </div>
      <input
        v-model="user.name"
        type="text"
        class="custom-input mb-4"
        placeholder="Please type your name"
      />
      <input
        v-model="user.passcode"
        type="password"
        maxlength="4"
        class="custom-input mb-5"
        placeholder="Enter 4 digit passcode"
      />
      <p class="error" v-if="errorMessage !== ''">{{ errorMessage }}</p>
      <button @click="signIn" class="proceed-btn">
        Sign In
        <b-spinner class="action-spinner" v-if="loading"></b-spinner>
      </button>
    </div>
  </div>
</template>

<script>
import UserService from '../service/UserService';
import moment from 'moment';

export default {
  name: 'SignIn',
  props: ['userFromSignUp'],
  data() {
    return {
      user: {
        name: '',
        passcode: '',
      },
      errorMessage: '',
      loading: false,
    };
  },
  created() {
    if (this.userFromSignUp !== undefined) {
      this.user = this.userFromSignUp
    }
  },
  methods: {
    signIn() {
      this.loading = true;

      UserService.signin(this.user).then(
        (response) => {
          const user = response.data;
          user['$$codeName'] = user.name.charAt(0);
          user.modificationDate = moment().calendar();

          localStorage.setItem('user', JSON.stringify(user));

          this.$router.push({
            name: 'Overview',
            params: { user: user },
          });
          this.loading = false;
        },
        (error) => {
          this.errorMessage = error.response.data;
          this.loading = false;
        }
      );
    },
    directToSignUp() {
      this.$router.push({ name: 'SignUp' });
    },
  },
};
</script>

<style lang="scss" scoped>
@import '../assets/styles/main.scss';
</style>
